    padding: 1em;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    min-height: 220px;
}

.ec-store-block-product {
    background: none;
    display: inline-block;
}

.ec-store-block-product svg {
    margin: auto;
}

.ec-store-product-title {
    font-weight: 600;
    font-size: 14px;
    color: #555;
    margin-top: 5px;
}

.ec-store-block>div {
    display: flex;
    justify-content: center;
}

.ec-store-block .ec-store-block-header {
    font-weight: 600;
    margin-bottom: 1em;
    line-height: 24px;
}

.ec-store-block .ec-store-block-header svg {
    max-height: 24px;
    margin-right: 1ch;
}

.ec-store-block .ec-store-block-subheader {
    font-size: 16px;
    font-weight: bold;
    line-height: 24px;
    letter-spacing: -.1px;
    margin: 16px 0 0;
}

.ec-store-block .ec-store-block-subheader:first-child {
    margin-top: 0;
}

.ec-store-block .ec-store-store-block-icon,
.ec-store-block .ec-store-product-block-icon {
    margin-right: 10px;
}

.ec-store-block .ec-store-block-image {
    max-height: 149px;
    background-repeat: no-repeat;
    background-size: contain;
    background-position: 50% 50%;
}

.ec-store-block .ec-store-block-image img {
    width: auto;
    max-height: 100%;
    -webkit-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.1);
    object-fit: cover;
}

.ec-store-block-product .ec-store-block-image,
.ec-store-block-product .ec-store-block-product-preview {
    height: 180px;
    min-width: 180px;
}

.ec-store-block-product .ec-store-block-product-preview {
    border: 1px solid rgba(145, 151, 162, .25);
}

.ec-store-block-minicart {
    min-height: 180px;
    min-width: 180px;
    display: inline-block;
    background: none;
    position: relative;
}

.ec-store-block-minicart .image {
    background: url('./images/minicart-preview.png') 50% 50% no-repeat;
    background-size: 50%;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}

.ec-store-block .ec-store-block-title {
    margin-top: 11px;
    margin-bottom: 1em;
}

.ec-store-block .ec-store-block-button {
    font-weight: 400;
    margin-top: 11px;
}

.ec-store-block>.ec-store-products {
    display: block;
}

.ec-store-products {

    .ec-store-product-block {
        width: 130px;
        margin: 15px;
        display: inline-block;

        .ec-store-product {
            width: 130px;
            height: 130px;
            /*border: 1px solid #e2e4e7;*/
            background-size: 100% 100%;
        }

        .ec-store-stub-sample {
            width: 130px;
            height: 40px;
            margin: 0;
            background: url("./images/stub-sample.png") no-repeat;
            background-position: center;
        }

        &.ec-store-product-filter {
            vertical-align: top;

            .ec-store-stub-sample {
                height: 130px;
                background: url("./images/stub-filter.png") no-repeat;
                background-size: contain;
            }
        }
    }
}

