}

.ec-store-inspector-subheader-row {
    margin-bottom: 20px;
}

.ec-store-inspector-row {
    justify-content: space-between;
    display: flex;
    align-items: center;
    margin: 15px 0;

    label,
    button {
        display: block;
    }
}

.ec-store-inspector-subheader {
    color: #191e23;
    font-weight: 600;
    cursor: default;
}

.ec-store-inspector-select {
    max-width: 100%;
}

.ec-store-control-select {
    width: 100%;
}

.ec-store-inspector-button-group {
