    line-height: 24px;
    display: flex;
    color: #979797;
}

.ec-store-block .ec-store-block-header svg {
    max-height: 20px;
    margin-right: .5ch;
    fill: #979797;
}

.ec-store-generic-block,
.ec-store-block-categories {
    -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2);
}

.ec-store-inspector-radio .components-radio-control__option,
